body {
  font-family: sans-serif;
  margin: 0;
  background-color: black;
}

canvas {
  position: fixed;
  display: block;
  z-index: 1;
}

p {
  z-index: 50;
  top: 40vh;
  margin-top: 0;
  font-size: 10vmin;
  width: 50vw;
  text-align: center;
  font-family: 'Orbitron', monospace;
  font-weight: 800;

  color: rgba(255, 255, 255, 0.7);

  padding-left: 5vw;
  padding-right: 5vw;
  user-select: none;
}

.name{
  display: flex;
  flex-direction: row;
  width: 100vw;
  align-items: center;
  top: 55vh;
  position: fixed;
  z-index: 40;
}
